
.nav-bar {
    border-bottom: 0.5px solid #dddddd; 
    overflow: auto; 
    display: flex; 
    justify-content: space-between; 
    align-items: center; 
    margin: 10px; 
}

.nav-bar-logo {
    float: left; 
    width: 200px; 
    height: auto; 
    margin-left: 10px; 
    margin-right: 10px; 
}

.nav-bar-tabs {
    display: flex; 
}

.nav-bar-tab {
    font-family: 'Euphemia UCAS', sans-serif; 
    padding: 5px 10px; 
    cursor: pointer; 
    display: flex; 
    flex-direction: column; 
    align-items: center; 
    text-align: center; 
    justify-content: flex-end; 
    height: 100%; 
}

.nav-bar-tab.active {
    border-bottom: 3px solid #619346 !important; 
}

.nav-bar-tab:hover {
    border-bottom: 3px solid #61934677; 
    animation: cubic-bezier(1, 0, 0, 1); 
}